                <div class="upload__text">点击图标或将文件文件拖拽到此处上传</div>
                <div class="filestyle">支持文件格式：json等</div>
                
            </el-upload>
            </div>        
        </div>
        <!-- 占位 -->
        <div style="height:25px;"></div>
        <el-select v-model="selectedValue1" placeholder="请选择SBOM规范1" style="margin-right: 5%;">
