<script>
export default {
    // ...
    data(){
        return {
            selectDemo: false,
            fileList:[],
            handleSuccessflag:true,
            SBOM:['GEEKSBOM','CycloneDX','SPDX'],
            selectedValue2:'',
            selectedValue1:'',
        }
    },

    methods: {
        downloadSBOMc1() {
            let a = document.createElement("a");
            a.href = "/static/analysis.json";
            a.click();
        },
        handleAlert(){
            alert('您还不是会员');
            return false;
        },
        goToList(){
            if(this.selectDemo){
                this.$router.push('/SBOM/test');
            }
            else{
                alert('您还没有选中的项目！')
            }
        },

        //start
        handleSuccess(response,file, fileList) {

            if(!this.handleSuccessflag) return;

            const isAPK = response.raw.type === '.json';
            // const isAPK = response.raw.type === 'application/vnd.android.package-archive';
            console.log(response);
            if(isAPK){
            console.log(response.raw.name + "上传成功");
            this.fileList.push(file);
            this.$message({
                    message: '上传成功',
                    type: 'success'
                });

                setTimeout(() => {
                this.$message.closeAll();
            }, 2000);

            }

            else{
                this.fileList=[];
                this.$message.error('仅限上传json等格式文件');
            }
            
        },

        
        postFiles() {
            var _this=this

            if(this.fileList.length === 0){
                alert('请上传文件');
                return;
            }
            this.handleSuccessflag=false;
            this.$refs.el.submit();

            
            this.$message.info('即将生成');
            setTimeout(()=>{
                this.$router.push('/SBOM/test');
            },300);
            
        },

        //end
    
    },
}
</script>

